.header {
    .header__top-block {
        @include clr;
        .header__logo {
            float: left;
            width: percentage(220/1230);
            padding: 15px 0 15px 20px; }

        .search-form {
            width: percentage(645/1230);
            height: 30px;
            padding: 20px 0;
            display: inline-table;
            float: left;
            .search {
                width: percentage(615/645);
                height: 30px;
                .search__input {
                    border: 1px solid #dcdee0;
                    width: 100%;
                    height: 100%;
                    @include clr;
                    input {
                        font-family: $font-common;
                        font-size: em(12, 13);
                        padding-left: 15px;
                        float: left;
                        width: percentage(548/615);
                        height: 100%;
                        margin: 0; }
                    .search__btn {
                        float: right;
                        height: 100%;
                        margin: 0;
                        @include clr;
                        .icon {
                            float: left;
                            background-color: #fff;
                            border-left: 1px solid #dcdee0; }
                        .icon-arrow-down_large {
                            @include s(arrow-down_large); }
                        .icon-search {
                            @include s(search); } } } }

            .icon-settings {
                @include s(settings-gray);
                cursor: pointer;
                display: table-cell;
                vertical-align: middle; } }

        .user {
            float: right;
            padding: 15px 20px 15px 0;
            width: percentage(360/1230);
            @include clr;

            .user__icon {
                @include s(user-gray);
                float: right;
                cursor: pointer; }
            .user__login {
                float: right;
                font-size: em(12, 13);
                margin: 10px; }
            .user__logout {
                @extend %btn;
                border: 1px solid #dcdee0;
                float: right;
                margin-top: 8px; } } }

    .top-nav {
        border: 1px solid #dcdee0;
        ul {
            @include clr;
            .top-nav__item {
                float: left;
                padding: 12px 20px;

                &:first-of-type {
                    padding-left: 30px;
                    padding-right: 30px; }
                &:hover {
                    cursor: pointer; }
                &.active {
                    background-color: $bg-dark;
                    cursor: pointer;
                    .top-nav__item-text {
                        .text {
                            color: #fff; }
                        .icon-window {
                            @include s(window-white); }
                        .icon-file {
                            @include s(file-white); }
                        .icon-stack {
                            @include s(stack-white); }
                        .icon-star {
                            @include s(star-white); } } }


                .top-nav__item-text {
                    text-decoration: none;
                    .text {
                        text-transform: uppercase;
                        font-size: em(12, 13);
                        color: $dark-gray;
                        display: inline-block;
                        overflow: hidden;
                        padding: 0 5px; }
                    .extra {
                        padding: 0 5px;
                        margin-left: 3px;
                        background-color: #ff6c32;
                        border: none;
                        border-radius: 10px;
                        font-family: $font-regular;
                        font-size: em(10, 13);
                        font-weight: 400;
                        color: #fff;
                        display: inline-block;
                        overflow: hidden; }
                    .icon {
                        display: inline-block;
                        overflow: hidden; }
                    .icon-window {
                        @include s(window-gray); }
                    .icon-file {
                        @include s(file_gray); }
                    .icon-stack {
                        @include s(stack-gray); }
                    .icon-star {
                        @include s(star-gray); } } } } } }

