//BEGIN -- General Styles
html {
    height: 100%; }

body {
    background: #fff;
    font-family: $font-common;
    font-size: em(13);
    font-weight: 600;
    color: $dark-gray;
    height: 100%;
    overflow-y: scroll; }
//END -- General Styles

// BEGIN -- Styles for each SECTION
.section {
    background-color: #fff;
    border: 1px solid $border-gray;
    width: 100%;
    margin: 15px 0;
    padding: 10px;
    &.highlighted {
        background-color: $bg-light;
        border-color: $bg-light; }

    .section__meta-top {
        @include clr;
        .section__trigger {
            @extend %btn;
            padding: 10px;
            overflow: hidden;
            cursor: pointer;
            transition: 0.5s;
            &.up {
                transform: rotateX(180deg);
                background-position: -26px -127px; } }
        .section__heading {
            display: inline-block;
            margin-left: 15px;
            overflow: hidden;
            font-family: $font-bold;
            font-size: em(15, 13); }
        .section__btn {
            @extend %btn;
            float: right;
            display: inline-block; }
        .icon-arrow-down {
            @include s(arrow-down-gray);
            background-position: -26px -128px; } }
    .section__block {
        .section__nav {
            margin-top: 8px;
            @include clr;
            .item {
                display: inline-block;
                height: 30px;
                .label {
                    @extend %label; } } }

        .section__content {
            .no-data {
                background-color: $bg-very-light;
                padding: 20px 55px;
                margin-top: 8px;
                text-align: center; } } } }

// END -- Styles for each SECTION

// BEGIN -- Styles for page wrapper
.page-wrapper {
    flex: 1 0 auto;
    @include clr;
    .page-header {
        margin: 25px 20px 10px;
        @include clr;
        .page-header__info {
            float: left;
            .icon-folder-double {
                @include s(folder-double); }
            .info__heading {
                display: inline-block;
                padding: 0 15px;
                .info__title {
                    font-family: $font-regular;
                    font-size: em(20, 13);
                    font-weight: 300;
                    text-transform: uppercase; } } }
        .page-header__btn {
            float: right;
            .btn {
                @extend %btn;
                padding: 15px; }
            .icon-sliders {
                @include s(sliders-gray);
                background-position: -19px -103px; }
            .icon-star {
                @include s(star-gray);
                background-position: -34px -45px; } } }

    .page-main {
        width: percentage(730/1230);
        margin-left: 20px;
        margin-right: 10px;
        float: left; }

    .page-aside {
        width: percentage(450/1230);
        margin-right: 20px;
        float: right; } }
// END -- Styles for page wrapper


